.timer {
  margin-left: 15px;
  display: flex;
}

.timer .field {
  background-color: #f0fff0;
  border: 1px solid;
  border-radius: 25px;
  margin-top: 10px;
  margin-right: 20px;
  padding: 10px;
}

span {
  font-family: Inter, sans-serif;
  font-size: 30px;
}

.timer-input {
  width: 250px;
  height: 45px;
  border-radius: 10px;
  margin-left: 15px;
  font-family: Inter, sans-serif;
  font-size: 18px;
}

.timer-button {
  cursor: pointer;
  width: 15%;
  background-color: #f0fff0;
  border: 1px solid;
  border-radius: 16px;
  margin-bottom: 16px;
  margin-left: 15px;
  padding: 8px;
  font-family: Inter, sans-serif;
  font-size: 20px;
}

